body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1{
  color:#7ad7f0;
  padding-bottom: 0.5rem;
}

h4{
  color:#7ad7f0;
}

ul{
  text-align: left;
}

footer{
  padding-top: 2rem;
  padding-bottom: 2rem;
}


.body{ 
  background: #03396c;
  text-align: center;
  min-height: 100vh;
}

.sectionContainer{
  padding-top: 1rem;
  padding-bottom: 1rem;
  margin-left: 20%;
  margin-right: 20%;
}

.tabWrapper{
  background: #000000;
}

.footerLink{
  padding-left: 1rem;
  padding-right: 1rem;
}

#bannerContainer{
  position: relative;
  background: white;
}

#bannerImage{
  max-width: 100%;
}

#aboutMeRow{
  padding: 1rem;
}

#headshot{
  max-width: 100%;
  max-height: 100%;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}


